import {
    Box,
    makeStyles,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableRow,
    TableSortLabel
} from '@material-ui/core';
import { Pagination } from '@material-ui/lab';
import EmptyData from 'app/components/EmptyData';
import _ from 'lodash';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import SVG from 'react-inlinesvg';
import { FormattedMessage } from 'react-intl';
import { toAbsoluteUrl } from '_metronic/_helpers';

const useStyles = makeStyles(theme => ({
    root: {},
    pagination: {
        display: 'flex',
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        justifyContent: 'flex-end'
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1
    },
    title: {
        fontSize: '1.15rem',
        fontWeight: '500'
    },
    content: {
        fontSize: '1rem'
    }
}));

function AuthServiceTable(props) {
    const {
        columns,
        rows,
        page,
        lastpage,
        onPageChange,
        onDelete,
        onViewEdit,
        isPagination,
        isAction,
        isDelete,
        isViewEdit,
        additionalActions,
        onSort = null,
        isIndex
    } = props;
    const classes = useStyles();

    const handlePageChange = (e, newPage) => {
        if (onPageChange) onPageChange(newPage);
    };

    const handleDelete = id => {
        if (onDelete) onDelete(id);
    };

    const handleViewEdit = id => {
        if (onViewEdit) onViewEdit(id);
    };

    const getCellElement = (row, column) => {
        if (column.render) return column.render(_.get(row, column.id), row);

        return _.get(row, column.id);
    };

    const columnsSlice = columns.slice(1);

    const [order, setOrder] = React.useState('asc');
    const [orderBy, setOrderBy] = useState('');

    const handleSort = property => {
        const isAsc = orderBy === property && order === 'asc';
        const orderByTemp = isAsc ? 'desc' : 'asc';
        setOrder(orderByTemp);
        setOrderBy(property);
        if (onSort) onSort(property, orderByTemp);
    };

    return (
        <div>
            <div style={{ overflow: 'auto' }}>
                <Box>
                    <Table
                        style={{ minWidth: rows.length <= 0 ? '' : '1050px' }}
                    >
                        <TableHead>
                            <TableRow>
                                {isIndex && (
                                    <TableCell>
                                        <span className={classes.title}>
                                            <FormattedMessage id="GLOBAL.NO" />
                                        </span>
                                    </TableCell>
                                )}

                                {columnsSlice.map(column => (
                                    <TableCell key={column.id}>
                                        {column.isSort ? (
                                            <TableSortLabel
                                                active={orderBy === column.id}
                                                direction={
                                                    orderBy === column.id
                                                        ? order
                                                        : 'asc'
                                                }
                                                onClick={() =>
                                                    handleSort(column.id)
                                                }
                                            >
                                                <span className={classes.title}>
                                                    {column.title}
                                                </span>
                                                {orderBy === column.id &&
                                                column.disablePadding ? (
                                                    <span
                                                        className={
                                                            classes.visuallyHidden
                                                        }
                                                    >
                                                        {order === 'desc'
                                                            ? 'sorted descending'
                                                            : 'sorted ascending'}
                                                    </span>
                                                ) : null}
                                            </TableSortLabel>
                                        ) : (
                                            <span className={classes.title}>
                                                {column.title}
                                            </span>
                                        )}
                                    </TableCell>
                                ))}

                                {isAction && (
                                    <TableCell className="text-center">
                                        <span className={classes.title}>#</span>
                                    </TableCell>
                                )}
                            </TableRow>
                        </TableHead>
                        {rows.length > 0 && (
                            <TableBody>
                                {rows.map((row, index) => (
                                    <TableRow
                                        hover
                                        key={index}
                                        style={{
                                            backgroundColor:
                                                index % 2 === 1 ? '' : '#e2e3ef'
                                        }}
                                    >
                                        {isIndex && (
                                            <TableCell>{index + 1}</TableCell>
                                        )}

                                        {columnsSlice.map(column => (
                                            <TableCell key={column.id}>
                                                <span
                                                    className={classes.content}
                                                >
                                                    {getCellElement(
                                                        row,
                                                        column
                                                    )}
                                                </span>
                                            </TableCell>
                                        ))}

                                        {isAction && (
                                            <TableCell
                                                style={{ width: '12%' }}
                                                className="text-center"
                                            >
                                                {additionalActions.map(
                                                    (action, idx) => (
                                                        <React.Fragment
                                                            key={`action-item-${idx}`}
                                                        >
                                                            {action(row)}
                                                        </React.Fragment>
                                                    )
                                                )}
                                                {isViewEdit && (
                                                    <button
                                                        className="btn btn-icon btn-light btn-hover-primary btn-sm mx-3"
                                                        onClick={() =>
                                                            handleViewEdit(
                                                                row[
                                                                    columns[1]
                                                                        .id
                                                                ]
                                                            )
                                                        }
                                                    >
                                                        <span className="svg-icon svg-icon-md svg-icon-primary">
                                                            <SVG
                                                                src={toAbsoluteUrl(
                                                                    '/media/svg/icons/Communication/Write.svg'
                                                                )}
                                                            ></SVG>
                                                        </span>
                                                    </button>
                                                )}
                                                {isDelete && (
                                                    <button
                                                        className="btn btn-icon btn-light btn-hover-danger btn-sm"
                                                        onClick={() =>
                                                            handleDelete(
                                                                row[
                                                                    columns[1]
                                                                        .id
                                                                ]
                                                            )
                                                        }
                                                    >
                                                        <span className="svg-icon svg-icon-md svg-icon-danger">
                                                            <SVG
                                                                src={toAbsoluteUrl(
                                                                    '/media/svg/icons/General/Trash.svg'
                                                                )}
                                                            ></SVG>
                                                        </span>
                                                    </button>
                                                )}
                                            </TableCell>
                                        )}
                                    </TableRow>
                                ))}
                            </TableBody>
                        )}
                    </Table>
                </Box>
                {rows.length <= 0 && <EmptyData />}
            </div>
            {isPagination && (
                <Pagination
                    className={classes.pagination}
                    count={lastpage}
                    page={page}
                    shape="rounded"
                    onChange={handlePageChange}
                />
            )}
        </div>
    );
}

AuthServiceTable.propTypes = {
    className: PropTypes.string,
    columns: PropTypes.array,
    rows: PropTypes.array,
    onPageChange: PropTypes.func,
    onDelete: PropTypes.func,
    onViewEdit: PropTypes.func,
    page: PropTypes.number,
    lastpage: PropTypes.number,
    isPagination: PropTypes.bool,
    isAction: PropTypes.bool,
    isDelete: PropTypes.bool,
    isViewEdit: PropTypes.bool,
    isIndex: PropTypes.bool,
    additionalActions: PropTypes.array
};

AuthServiceTable.defaultProps = {
    className: '',
    columns: [],
    rows: [],
    onPageChange: null,
    onDelete: null,
    onViewEdit: null,
    page: 1,
    lastpage: 1,
    isPagination: true,
    isAction: true,
    isDelete: true,
    isViewEdit: true,
    isIndex: true,
    additionalActions: []
};

export default AuthServiceTable;
